export default "body {\r\n    margin: 0;\r\n    padding: 0;\r\n    background: #020306;\r\n    font-family: sans-serif;\r\n}\r\n\r\n.card {\r\n    position: relative;\r\n    top: 50%;\r\n    left: 50%;\r\n    transform: translate(-50%,-50%);\r\n    width: 360px;\r\n    height: 400px;\r\n    background: #000;\r\n}\r\n\r\n.card .image {\r\n    width: 360px;\r\n    height: 400px;\r\n    overflow: hidden;\r\n}\r\n\r\n.card .image img {\r\n    width: 100%;\r\n    transition: .5s;\r\n}\r\n\r\n.card:hover .image img {\r\n    opacity: .5;\r\n    transform: translateX(30%);/*100%*/\r\n}\r\n\r\n.card .details {\r\n    position: absolute;\r\n    top: 0;\r\n    left: 0;\r\n    width: 70%;/*100%*/\r\n    height: 100%;\r\n    background: #ffc107;\r\n    transition: .5s;\r\n    transform-origin: left;\r\n    transform: perspective(2000px) rotateY(-90deg);\r\n}\r\n\r\n.card:hover .details {\r\n    transform: perspective(2000px) rotateY(0deg);\r\n}\r\n\r\n.card .details .center {\r\n    padding: 20px;\r\n    text-align: center;\r\n    background: #020306;\r\n    position: absolute;\r\n    top: 50%;\r\n    transform: translateY(-50%);\r\n}\r\n\r\n.card .details .center h1 {\r\n    margin: 0;\r\n    padding: 0;\r\n    color: #ffd005;\r\n    line-height: 20px;\r\n    font-size: 20px;\r\n    text-transform: uppercase;\r\n}\r\n\r\n.card .details .center h1 span {\r\n    font-size: 14px;\r\n    color: rgba(244, 245, 255, 0.65);\r\n}\r\n\r\n.card .details .center p {\r\n    margin: 10px 0;\r\n    padding: 0;\r\n    color: rgba(255, 255, 255, 0.96);\r\n}\r\n\r\n.card .details .center ul {\r\n    margin: 10px auto 0;\r\n    padding: 0;\r\n    display: table;\r\n}\r\n\r\n.card .details .center ul li {\r\n    list-style: none;\r\n    margin: 0 5px;\r\n    float: left;\r\n}\r\n\r\n.card .details .center ul li a {\r\n    display: block;\r\n    background: #262626;\r\n    color: #fff;\r\n    width: 30px;\r\n    height: 30px;\r\n    line-height: 30px;\r\n    text-align: center;\r\n}\r\n\r\n.card .